import React from 'react';
import PropTypes from 'prop-types';
import Drawer from '@material-ui/core/Drawer';
import Box from '@material-ui/core/Box'
import Paper from '@material-ui/core/Paper'
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { Link } from 'gatsby';
import withRoot from '../../withRoot';
import Grid from '@material-ui/core/Grid';
import { ThemeProvider } from '@material-ui/styles';
import { makeStyles } from '@material-ui/core/styles';
import { themeHQ } from '../../styles/theme.ts';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/AddRounded';
import DeleteIcon from '@material-ui/icons/DeleteRounded';
import MoreHorizIcon from '@material-ui/icons/MoreHorizRounded';
import CloseIcon from '@material-ui/icons/CloseRounded';

// components
import TopNav from "../../components/topnav"

const useStyles = makeStyles(theme => ({
  root: {
    ...theme.typography.button,
    maxWidth: 1080,
    margin: 'auto',
    overflowX: 'auto',
  },

  section: {
    margin: themeHQ.spacing(3),
    padding: themeHQ.spacing(6),
    backgroundColor: themeHQ.palette.background.paper,
  },

  sectionHeader: {
    borderBottom: 'solid 1px #e9e9e9',
    paddingBottom: themeHQ.spacing(1),
    marginBottom: themeHQ.spacing(2)
  },

  extraSpacing: {
    marginBottom: themeHQ.spacing(2)
  },

  layout: {
    marginBottom: themeHQ.spacing(3),
    '& $button': {
      marginBottom: themeHQ.spacing(2)
    },
    '& $button+button': {
      marginLeft: themeHQ.spacing(2),
    }
  },

  leftIcon: {
    marginRight: themeHQ.spacing(1)-4,
  },

  panel:{
    // marginTop: 50,
    width: 480,
    height: '100%'
  }, 

  panelContainer:{
    height: 'inherit'
  },

  panelBody:{
    flexGrow: 1
  },

  textField: {
    // marginLeft: theme.spacing(1),
    // marginRight: theme.spacing(1),
    // width: 200,    
  },

  elementSpacing: {
    marginRight: themeHQ.spacing(2)
  }
}));

function createData(c1, c15, c2, c3, c4, c5) {
  return { c1, c15, c2, c3, c4, c5 };
}

const rows = [
  createData('Belgium', null, null, 'Payworks',  ),
  createData('Czech', '#123445', 'Prague Plaza', 'Adyen',),
  createData('Germany #1', '#123474', 'Berlin Flagship', 'Adyen',),
  createData('Germany #2', '#123472', 'Munich Townhall', 'Adyen',),
  createData('Germany #3', '#123477', 'Frankfurt International Airport', 'Adyen',),
];

const currencies = [
  {
    value: 'USD',
    label: '$',
  },
  {
    value: 'EUR',
    label: '',
  },
  {
    value: 'BTC',
    label: '',
  },
  {
    value: 'JPY',
    label: '',
  },
];


function About(props) {
  // const { classes } = props;
  const classes = useStyles();

  const [anchorEl, setAnchorEl] = React.useState(null);

  function handleClick(event) {
    setAnchorEl(event.currentTarget);
  }
  
  function handleClose() {
    setAnchorEl(null);
  }

  const [state, setState] = React.useState({
    edit: false,
  });

  const toggleDrawer = (item, open) => event => {
    // if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
    //   return;
    // }
    setState({ ...state, [item]: open });
    if (open === true){
      handleClose();
    }
  };

  const [values, setValues] = React.useState({
    name: 'Columbus Circle',
    age: '',
    multiline: 'Controlled',
    currency: 'EUR',
  })


  const handleChange = name => event => {
    setValues({ ...values, [name]: event.target.value });
  };
  

  const editPanel = item => (
    <Paper className={classes.panel}>
      <Grid container direction='column' className={classes.panelContainer}>
        <Grid item>
          <Box pt={6} p={2} borderBottom={1} borderColor={themeHQ.palette.divider} display='flex' justifyContent='space-between' alignItems='center'>
            <Typography variant='h3' display='inline'> Edit Item </Typography>
            <IconButton aria-label="Close" onClick={toggleDrawer('edit', false)}>
                <CloseIcon fontSize="small" />
            </IconButton>
          </Box>
        </Grid>   
        <Grid item className={classes.panelBody}>
          <Box p={2}>
            <Typography variant='caption'> Placeholder </Typography>
          </Box>
        </Grid> 
        <Grid item>
          <Box p={2} borderTop={1} borderColor={themeHQ.palette.divider} display='flex' justifyContent='flex-end'>
            <Box mr={2}><Button variant='outlined' color='secondary' onClick={toggleDrawer('edit', false) }>Cancel</Button></Box>
            <Button variant='contained' color='primary' onClick={toggleDrawer('edit', false)}>Save</Button>
          </Box>
        </Grid>   
      </Grid>
    </Paper>
  )


  return (
    <div className={classes.root}>
      <TopNav />  
      {/* <SideNav /> */}


      <ThemeProvider theme={themeHQ}>
        <Typography gutterBottom>
          <Link to="styleguides/adminconsole">Admin Console</Link>
        </Typography>
        <Typography variant='h1' display='block' align='center'>HQ Theme Overrides</Typography>

        <section className={classes.section}>
          <Typography variant='h2' gutterBottom className={classes.sectionHeader}>Typography</Typography>
          <Grid container direction='column'>
            <Grid item><Typography variant='h1'>Header 1 - View Title</Typography></Grid>
            <Grid item><Typography variant='subtitle1' className={classes.extraSpacing}>Subtitle 1 - View Subtitle / Description</Typography></Grid>
            <Grid item><Typography variant='h2'>Header 2 - Panel Title</Typography></Grid>
            <Grid item><Typography variant='subtitle2'  className={classes.extraSpacing}>Subtitle 2 - Panel Subtitle / Description</Typography></Grid>
            <Grid item><Typography variant='h3'  className={classes.extraSpacing}>Header 3 - Section Title</Typography></Grid>
            <Grid item><Typography variant='h4' className={classes.extraSpacing}>Header 4 </Typography></Grid>
            <Grid item><Typography variant='body1' className={classes.extraSpacing}>Body 1 - Description</Typography></Grid>
            <Grid item><Typography variant='body2' className={classes.extraSpacing}>Body 2 - Description - Small </Typography></Grid>
            <Grid item><Typography variant='caption' className={classes.extraSpacing}>Caption Text </Typography></Grid>
          </Grid>
        </section>

        <section className={classes.section}>
          <Typography variant='h2' gutterBottom className={classes.sectionHeader}>Buttons</Typography>
          <Grid container direction='column'>
            <Grid item className={classes.layout}>
              <Button color="primary" variant='contained' >Primary</Button>
              <Button color="primary" variant='contained' disabled >Primary Disabled</Button>
              <Button color="secondary" variant='contained' size='small' >Secondary Contained - Not Used</Button>
              <Button color="secondary" variant='contained' size='small' disabled >Secondary Contained Disbaled - Not Used</Button>
            </Grid>
            <Grid item className={classes.layout}>
              <Button color="primary" variant='outlined' >Primary Outlined</Button>
              <Button color="primary" variant='outlined' disabled >Primary Outlined Disabled</Button>
              <Button color="secondary" variant='outlined' >Secondary</Button>
              <Button color="secondary" variant='outlined' disabled >Secondary Disbaled</Button>
            </Grid>
            <Grid item className={classes.layout}>
              <Button color="primary" variant='text' >Primary Text Button </Button>
              <Button color="primary" variant='text' disabled >Primary Text Button Disabled</Button>
              <IconButton aria-label="Add" color='primary'>
                <AddIcon fontSize="small" />
              </IconButton>
              <IconButton aria-label="Delete" color='secondary' >
                <DeleteIcon fontSize="small" />
              </IconButton>
              <IconButton aria-label="Delete" disabled >
                <DeleteIcon fontSize="small" />
              </IconButton>
            </Grid>
            <Grid item className={classes.layout}>
              <Button variant="text" color="primary" >
                <AddIcon fontSize="small"  className={classes.leftIcon}/>
                Add Item
              </Button>
              <Button variant="text" color="primary" disabled >
                <AddIcon fontSize="small"  className={classes.leftIcon}/>
                Add Item
              </Button>
              <Button variant="text" color="secondary" >
                <DeleteIcon fontSize="small"  className={classes.leftIcon} />
                Delete
              </Button>
              <Button variant="text" color="secondary" disabled >
                <DeleteIcon fontSize="small"  className={classes.leftIcon} />
                Delete
              </Button>
            </Grid>
          </Grid>
        </section>


        <section className={classes.section}>
        <Box className={classes.root}>
          <Typography variant='h2' gutterBottom className={classes.sectionHeader}>Table, Menu, Drawer</Typography>
          <Table minWidth='640'>
            <TableHead>
              <TableRow>
                <TableCell>Merchant</TableCell>
                <TableCell align="left">Store</TableCell>
                <TableCell align="left">Store ID</TableCell>
                <TableCell align="left">Provider</TableCell>
                <TableCell align="right">Action</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map(row => (
                <TableRow key={row.c1} hover>
                  <TableCell component="th" scope="row">
                    <Typography>{row.c1}</Typography>
                  </TableCell>
                  <TableCell align="left">
                    {row.c15 != null &&  <Typography noWrap maxWidth='240'>{row.c2}</Typography> }
                  </TableCell>
                  <TableCell align="left">
                    {row.c15 != null &&  <Typography>{row.c15}</Typography> }
                  </TableCell>
                  <TableCell align="left">
                    <Typography>{row.c3}</Typography>
                  </TableCell>
                  {/* <TableCell align="right">{row.c4}</TableCell> */}
                  <TableCell align="right">
                    <IconButton aria-label="more" aria-controls="table-row-menu" aria-haspopup="true" onClick={handleClick}>
                      <MoreHorizIcon fontSize="small" />
                    </IconButton>
                    <Menu
                      id="table-row-menu"
                      anchorEl={anchorEl}
                      keepMounted
                      open={Boolean(anchorEl)}
                      onClose={handleClose}
                    >
                      <MenuItem onClick={toggleDrawer('edit', true)}>Edit</MenuItem>
                      <MenuItem onClick={handleClose}>Duplicate</MenuItem>
                      <MenuItem onClick={handleClose} disabled>Disable</MenuItem>
                    </Menu>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
          </Box>
          <Drawer variant='persistent' anchor="right" open={state.edit} onClose={toggleDrawer('edit', false)}>
            {editPanel('edit')}
          </Drawer>
        </section>

        <section className={classes.section}>
          <Typography variant='h2' gutterBottom className={classes.sectionHeader}>Form Controls</Typography>
          <form className={classes.container} noValidate autoComplete="off">
          <Grid container direction='column'>

          <Typography variant='h3' gutterBottom>Text Input</Typography>
            <Grid item>
              <TextField
                id="standard-name"
                label="Store Name"
                value={values.name}
                onChange={handleChange('name')}
                margin="normal"
                className={classes.elementSpacing}
              />
              <TextField
                disabled
                id="standard-disabled"
                label="Store Name Disabled"
                defaultValue="Tulip Online"
                margin="normal"
              />
            </Grid>
          </Grid>
          </form>
        </section>


      </ThemeProvider>
    </div>
  );
}

About.propTypes = {
  classes: PropTypes.object.isRequired,
};

// export default withRoot(withStyles(styles)(About));
export default withRoot(About);
